import { FastMap } from "../util/FastMap";
import { Kernel } from "./Kernel";
import { PipelineListener } from "./PipelineListener";
import { Packet } from "./Packet";

/**
 * 
 * @callback ResponseCallback
 * @param {Pipeline} pipeline 
 * @param {string} source 
 * @param {Packet} packet 
 */

/**
 * 
 */
export class Pipeline {

    /**
     * 
     * @param {string} name 
     */
    constructor(name) {
        /**
         * 
         * @type {string}
         */
        this.name = name;

        /**
         * 
         * @type {Kernel}
         */
        this.kernel = null;

        /**
         * 
         * @type {FastMap<string,PipelineListener|function>}
         */
        this.listenerMap = new FastMap();

        /**
         * 
         * @type {string}
         */
        this.address = null;
    
        /**
         * 
         * @type {number}
         */
        this.port = 7070;

        /**
         * 
         * @protected
         * @type {boolean}
         */
        this.opened = false;

        /**
         * 
         * @type {string}
         */
        this.tokenCode = null;
    }

    /**
     * 
     * @returns {string} 
     */
    getName() {
        return this.name;
    }

    /**
     * 
     * @param {string} address 
     * @param {number} port 
     */
    setRemoteAddress(address, port) {
        if (this.address == address && this.port == port) {
            return;
        }

        let reset = this.opened;

        if (reset) {
            this.close();
        }

        this.address = address;
        this.port = port;

        if (reset) {
            this.open();
        }
    }

    /**
     * 
     * @param {string} tokenCode
     */
    setTokenCode(tokenCode) {
        this.tokenCode = tokenCode;
    }

    /**
     * 
     */
    open() {
        this.opened = true;
    }

    /**
     * 
     */
    close() {
        this.opened = false;
    }

    /**
     * 
     * @returns {boolean}  {@linkcode true} 
     */
    isReady() {
        // Nothing
        return true;
    }

    /**
     * 
     * @param {string} destination 
     * @param {Packet} packet 
     * @param {ResponseCallback} [handleResponse] 
     */
    send(destination, packet, handleResponse) {
        // Nothing
    }

    /**
     * 
     * @param {string} destination 
     * @param {PipelineListener} listener 
     */
    addListener(destination, listener) {
        let listeners = this.listenerMap.get(destination);
        if (null == listeners) {
            this.listenerMap.put(destination, [ listener ]);
        }
        else {
            let index = listeners.indexOf(listener);
            if (index < 0) {
                listeners.push(listener);
            }
        }
    }

    /**
     * 
     * @param {string} destination 
     * @param {PipelineListener} listener 
     */
    removeListener(destination, listener) {
        let listeners = this.listenerMap.get(destination);
        if (null == listeners) {
            return;
        }

        let index = listeners.indexOf(listener);
        if (index >= 0) {
            listeners.splice(index, 1);
        }
    }

    /**
     * 
     * @protected
     * @param {string} destination 
     * @returns {Array<PipelineListener>} 
     */
    getListeners(destination) {
        return this.listenerMap.get(destination);
    }

    /**
     * 
     * @protected
     * @param {string} source 
     * @param {Packet} packet 
     */
    triggerListeners(source, packet) {
        let listeners = this.listenerMap.get(source);
        if (null == listeners) {
            return;
        }

        for (let i = 0; i < listeners.length; ++i) {
            let listener = listeners[i];
            if (typeof listener === 'function') {
                listener(this, source, packet);
            }
            else {
                listener.onReceived(this, source, packet);
            }
        }
    }

    /**
     * 
     * @protected
     * @param {string} source 
     * @param {Packet} packet 
     * @param {ResponseCallback} callback 
     */
    triggerCallback(source, packet, callback) {
        callback(this, source, packet);
    }
}
