import { Subject } from "./Subject";
import { Kernel } from "./Kernel";
import { Pipeline } from "./Pipeline";
import { PluginSystem } from "./PluginSystem";

/**
 * 
 */
export class Module extends Subject {

    /**
     * 
     * @param {string} name 
     */
    constructor(name) {
        super();

        /**
         * 
         * @protected
         * @type {string}
         */
        this.name = name;

        /**
         * 
         * @protected
         * @type {Kernel}
         */
        this.kernel = null;

        /**
         * 
         * @protected
         * @type {Pipeline}
         */
        this.pipeline = null;

        /**
         * 
         * @protected
         * @type {boolean}
         */
        this.started = false;

        /**
         * 
         * @protected
         * @type {Array<string>}
         */
        this.dependencies = [];

        /**
         * 
         * @protected
         * @type {Array<string>}
         */
        this.deps = [];

        /**
         * 
         * @protected
         * @type {PluginSystem}
         */
        this.pluginSystem = new PluginSystem();
    }

    /**
     * 
     * @returns {string} 
     */
    getName() {
        return this.name;
    }

    /**
     * 
     * @param {string} moduleName 
     */
    require(moduleName) {
        let index = this.dependencies.indexOf(moduleName);
        if (index >= 0) {
            return;
        }

        this.dependencies.push(moduleName);
    }

    /**
     * 
     * @param {string} lib 
     */
    requireFile(lib) {
        let index = this.deps.indexOf(lib);
        if (index >= 0) {
            return;
        }

        this.deps.push(lib);
    }

    /**
     * 
     * @returns {boolean}  {@linkcode true}  {@linkcode false} 
     */
    hasStarted() {
        return this.started;
    }

    /**
     * 
     * @returns {boolean}  {@linkcode false} 
     */
    start() {
        // 
        if (!this.kernel.working) {
            return false;
        }

        // 
        if (this.started) {
            return false;
        }

        // 
        for (let i = 0; i < this.dependencies.length; ++i) {
            let mod = this.kernel.getModule(this.dependencies[i]);
            if (null != mod) {
                mod.start();
            }
        }

        this.started = true;
        return true;
    }

    /**
     * 
     */
    stop() {
        this.started = false;
    }

    /**
     * 
     */
    suspend() {
        // Nothing
    }

    /**
     * 
     */
    resume() {
        // Nothing
    }

    /**
     * 
     * @returns {PluginSystem} 
     */
    getPluginSystem() {
        return this.pluginSystem;
    }
}
